<template>
    <div>
        <top-nav title="管理员列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>这里是管理员列表</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form label-width="80px" size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="管理员名(手机号)" label-width="100">
                        <el-input v-model="searchForm.manage_name"></el-input>
                    </el-form-item>

                    <el-form-item label="添加时间">
                        <el-date-picker v-model="searchForm.bg_add_time" type="datetime" placeholder="选择日期时间"></el-date-picker>至
                        <el-date-picker v-model="searchForm.end_add_time" type="datetime" placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="上次登录">
                        <el-date-picker v-model="searchForm.bg_last_time" type="datetime" placeholder="选择日期时间"></el-date-picker>至
                        <el-date-picker v-model="searchForm.end_last_time" type="datetime" placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="是否锁定">
                        <el-select v-model="searchForm.is_lock" placeholder="请选择">
                            <el-option :key="-100" label="请选择" :value=-100></el-option>
                            <el-option :key="0" label="未锁定" :value=0></el-option>
                            <el-option :key="1" label="已锁定" :value=1></el-option>
                        </el-select>
                    </el-form-item>

                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getUserList(1)" size="mini" type="primary">搜索</el-button>
                        <a href="javascript:void(0);" @click="searchForm.manage_name = '',searchForm.is_lock = -100,
            searchForm.bg_add_time = '',searchForm.end_add_time = '',searchForm.bg_last_time = '',searchForm.end_last_time = ''">取消搜索</a>
                        <el-button size="mini" type="danger" @click="createUser(false)">新增管理员</el-button>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>

                    <el-table-column prop="manage_id" label="管理员ID" width="80"></el-table-column>

                    <el-table-column prop="manage_name" label="用户名"></el-table-column>

                    <el-table-column prop="remark" label="备注"></el-table-column>

                    <el-table-column label="是否锁定">
                        <template slot-scope="scope">
                            <el-switch @change="lockUser(scope.row)"  v-model="scope.row.is_lock" :active-value="1" :inactive-value="0"></el-switch>
                        </template>
                    </el-table-column>

                    <el-table-column label="最近登录" >
                        <template slot-scope="scope">
                            <p>登录时间：{{scope.row.last_time}}</p>
                            <p>登录IP：{{scope.row.last_ip}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="注册信息" >
                        <template slot-scope="scope">
                            <p>注册时间：{{scope.row.add_time}}</p>
                            <p>注册IP：{{scope.row.add_ip}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <el-button @click="editManager(scope)" size="mini" type="text">编辑</el-button>
                            <el-popover style="margin: 0 10px;" placement="top" width="160"
                                        v-model="scope.row.is_delete">
                                <p style="margin-bottom: 4px">确定删除吗？</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button  :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消
                                    </el-button>
                                    <el-button :loading="loading" type="primary" size="mini" @click="deleteUser(scope)">确定
                                    </el-button>
                                </div>
                                <el-button type="text" slot="reference">删除</el-button>
                            </el-popover>
                            <el-button @click="lockUser(scope.row)" size="mini" type="text">{{scope.row.is_lock ? '解锁' : '锁定'}}</el-button>
                        </template>
                    </el-table-column>

                </el-table>
                <div class="page">

                    <div class="page-mian">
                        <el-pagination @current-change="getUserList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>


        <!--编辑新增 弹窗-->
        <el-dialog title="管理员" v-loading="subLoading" :visible.sync="dialog" width="30%" :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small">
                <el-form-item label="账号" label-width="80px">
                    <el-input v-model="form.manage_name" autocomplete="off" :disabled="manageId > 0"></el-input>
                </el-form-item>
                <el-form-item label="密码" label-width="80px">
                    <el-input v-model="form.password" autocomplete="off" placeholder="编辑管理员时留空代表不修改密码"></el-input>
                </el-form-item>
                <el-form-item label="备注" label-width="80px">
                    <el-input v-model="form.remark" autocomplete="off" placeholder="备注"></el-input>
                </el-form-item>
                <el-form-item label="是否锁定" label-width="80px">
                    <el-radio v-model="form.is_lock" :label=0>不锁定</el-radio>
                    <el-radio v-model="form.is_lock" :label=1>锁定</el-radio>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false,addUser = true">取 消</el-button>
                <el-button size="small" type="primary" @click="createUser(true)">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'adminRole',
        components: {TopNav},
        data() {
            return {
                //修改 添加 弹窗  菊花
                subLoading: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                //修改 添加 状态控制
                dialog: false,
                //修改 添加 form
                addUser: false,
                form: {
                    is_lock: 0,
                    manage_id: 0,
                    manage_name: '',
                    password: '',
                    remark: '',
                },
                //搜索 form
                searchForm: {
                    manage_name: '',  // 手机号码
                    is_lock: -100,
                    end_add_time: '',
                    bg_add_time: '',
                    end_last_time: '',
                    bg_last_time: '',
                },
                manageId: 0,
            }

        },
        created() {
            this.getUserList();
        },
        methods: {
            /**
             *
             */
            handleSelectionChange(e){
                this.memberIds = [];
                e.forEach((item,index) => {
                    this.memberIds.push(item.manage_id);
                });
            },
            /**
             * 获取管理员列表
             * @param page {number} 页数
             */
            getUserList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.manage.manage/index", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    manage_name: this.searchForm.manage_name,
                    is_lock: this.searchForm.is_lock,
                    end_add_time: this.searchForm.end_add_time,
                    bg_add_time: this.searchForm.bg_add_time,
                    end_last_time: this.searchForm.end_last_time,
                    bg_last_time: this.searchForm.bg_last_time,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            /**
             * 添加管理员
             * @param sub {boolean} 是否是 提交操作
             */
            createUser(sub = true) {
                if (sub == false) {
                    this.dialog = true;
                    this.form.manage_name = "";
                    this.form.password = "";
                    this.form.remark = '';
                    this.form.is_lock = 0;
                    this.manageId = 0;
                } else {
                    this.subLoading = true;
                    var url = "/shop/admin.manage.manage/create";
                    if (this.manageId > 0) {
                        url = "/shop/admin.manage.manage/edit";
                    }
                    console.log(url);
                    api.post(url, {
                        appToken: this.$store.getters.appToken,
                        manage_id: this.manageId,
                        manage_name: this.form.manage_name,
                        password: this.form.password,
                        remark: this.form.remark,
                        is_lock: this.form.is_lock,
                    }).then(res => {
                        this.form.role_name = "";
                        this.subLoading = false;
                        this.dialog = false;
                        this.$message({
                            message: '恭喜你，操作成功',
                            type: 'success',
                        });
                        this.getUserList();
                        this.manageId = 0;
                    }).catch((error) => {
                        this.subLoading = false;
                        this.dialog = false;
                    });
                }
            },

            /**
             * 删除管理员
             * @param row {data} 操作的记录数据对象
             */
            deleteUser(scope) {
                this.loading = true;
                api.post("/shop/admin.manage.manage/del", {
                    appToken: this.$store.getters.appToken,
                    manage_id:scope.row.manage_id
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.getUserList();
                }).catch((error) => {
                    this.loading = false;
                });
            },


            /**
             * 修改管理员弹出
             * @param row {data} 操作的记录数据对象
             */
            editManager(scope) {
                this.dialog = true;
                this.manageId = scope.row.manage_id;
                this.form.manage_name = scope.row.manage_name;
                this.form.manage_id = scope.row.manage_id;
                this.form.is_lock = scope.row.is_lock;
            },


            /**
             * 锁定/解锁管理员
             */
            lockUser(row) {
                this.loading = true;
                this.is_lock = !row.is_lock;
                api.post("/shop/admin.manage.manage/lock", {
                    appToken: this.$store.getters.appToken,
                    manage_id:row.manage_id,
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    row.is_lock = this.is_lock;
                    this.getUserList();
                }).catch((error) => {
                    this.loading = false;
                });
            },

        }
    }
</script>
